* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-list {
  padding: 0;
}
.title {
  font-size: 20px;
  margin-left: 5px;
}
.country_shortinfo {
  display: flex;
  margin-bottom: 10px;
}
.link {
  list-style: none;
}
.list {
  padding: 0;
  margin: 5px;
}
